<template>
  <el-menu class="sidebar-menu" :default-active="defaultMenu" @select="handleMenuSelect">
    <el-menu-item v-for="(item, index) in menus" :key="index" :index="item.key">
      <i :class="item.icon"></i>
      <span slot="title">{{item.name}}</span>
    </el-menu-item>
  </el-menu>
</template>

<script>
export default {
  props: {
    menus: {
      type: Array,
      required: true
    },
    defaultActive: {
      required: false
    }
  },
  data () {
    return {
      currentSelected: ''
    }
  },
  computed: {
    defaultMenu () {
      return this.defaultActive ? this.defaultActive : this.menus[0].key
    }
  },
  methods: {
    handleMenuSelect (index, indexPath) {
      this.currentSelected = index
    }
  },
  watch: {
    currentSelected (val, oldVal) {
      this.$emit('change', val)
    }
  },
  mounted () {
    this.currentSelected = this.defaultMenu
  }
}
</script>

<style lang="scss">

</style>



