<template>
  <el-container>
    <el-aside>
      <div class="logo">
        SHURI
      </div>
      <side-menu :menus="menus" @change="handleMenuIndexChange"></side-menu>
    </el-aside>
    <el-container>
      <el-header></el-header>
      <el-main></el-main>
    </el-container>
  </el-container>
</template>

<script>
import SideMenu from '@/components/SideMenu'

export default {
  components: {
    SideMenu
  },
  data () {
    return {
      menus: [
        { key: 'videoManage', name: '视频管理', icon: 'el-icon-caret-right' },
        { key: 'themeManage', name: '主题管理', icon: 'el-icon-star-off' },
        { key: 'categoryManage', name: '分类管理', icon: 'el-icon-tickets' },
        { key: 'templateManage', name: '模板管理', icon: 'el-icon-news' }
      ]
    }
  },
  methods: {
    handleMenuIndexChange (menuIndex) {
      console.log(menuIndex)
    }
  }
}
</script>

<style lang="scss">
  $header-height: 64px;
  .el-container {
    height: 100vh;
  }
  .el-aside {
    width: 220px !important;
    background: #3f4a59;
    color: rgba(255,255,255,0.7);
    box-shadow: 1px 0 15px 1px rgba(81,77,92,.5);

    .logo {
      height: $header-height;
      text-align: center;
      font-size: 40px;
      line-height: $header-height;
      color: #fff;
      font-family: fantasy;
      letter-spacing: 6px;
    }

    .el-menu {
      height: calc(100% - #{$header-height});
      overflow: auto;
      border-right: none;
      background: transparent;
    }
  }
  .el-main {
    color: #333;
  }
  .el-header, .el-footer {
    height: $header-height !important;
    color: #333;
    box-shadow: 4px 0 5px rgba(0,0,0,0.08)
  }
</style>



